function normalizeCollaborator(entry) {
    if (typeof entry === "string") {
        return entry;
    }
    if (entry && typeof entry === "object") {
        const login = entry.login;
        if (typeof login === "string") {
            return login;
        }
    }
    return null;
}
export async function fetchOrganizationCollaborators(context, org) {
    try {
        const collaborators = await context.octokit.paginate(context.octokit.rest.orgs.listMembers, {
            org,
            per_page: 100,
        });
        return collaborators.map(normalizeCollaborator).filter((login) => Boolean(login));
    }
    catch (error) {
        const cause = error instanceof Error ? error : new Error(String(error));
        context.logger.error(`Failed to fetch collaborators for ${org}`, { error: cause });
        return [];
    }
}
