import { getUserId } from "../../github/get-user-id";
export async function assignIssueToUser(context, repository, issue, login) {
    if (!issue?.number) {
        return false;
    }
    const issueUrl = `https://github.com/${repository.owner}/${repository.name}/issues/${issue.number}`;
    const issueRef = `${repository.owner}/${repository.name}#${issue.number}`;
    if (context.config.dryRun) {
        context.runSummary?.addAction(context.logger.info(`Dry run: would assign ${issueRef} to ${login}`, { repository, issue: issue.number, chosen: login }).logMessage.raw);
        return true;
    }
    const body = {
        issueUrl,
        userId: await getUserId(context.octokit, login),
    };
    try {
        const tokenInfo = (await context.octokit.auth({ type: "installation" }));
        const response = await fetch(`${context.env.START_STOP_ENDPOINT}/start`, {
            method: "POST",
            headers: {
                "Content-Type": "application/json",
                Authorization: `Bearer ${tokenInfo.token}`,
            },
            body: JSON.stringify(body),
        });
        if (!response.ok) {
            context.runSummary?.addAction(context.logger.warn(`Failed to assign ${issueRef} to ${login} (${response.status} ${response.statusText})`, {
                response: response.status,
                status: response.statusText,
                url: issueUrl,
            }).logMessage.raw);
            return false;
        }
        context.runSummary?.addAction(context.logger.ok(`Assigned ${issueRef} to ${login}`, {
            response: await response.json(),
        }).logMessage.raw);
        return true;
    }
    catch (err) {
        context.runSummary?.addAction(context.logger.error(`Failed to assign ${issueRef} to ${login}`, {
            err: String(err),
            url: issueUrl,
        }).logMessage.raw);
        return false;
    }
}
